<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <process
            :type="1"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process')"
            :handle-business="handleBusiness"
        />
        <car-input v-if="currentStep === steps.findIndex(step=>step.key === 'carInput')"/>
        <confirm-info v-if="currentStep === steps.findIndex(step=>step.key === 'confirmInfo')"/>
        <mail v-if="currentStep === steps.findIndex(step=>step.key === 'mail')"/>
        <upload v-if="currentStep === steps.findIndex(step=>step.key === 'upload')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <process
            :type="2"
            v-if="currentStep === steps.findIndex(step=>step.key === 'process2')"
            :handle-business="handleBusinessComplete"
        />
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="examptionCompleteInfo"
        />
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import process from '@/components/process';
import serverInfo from '@/components/server-info';
import carInput from './components/car-input';
import confirmInfo from './components/confirm-info';
import upload from './components/upload';
import mail from './components/address';
import checkPhone from './components/check-phone';
import complete from '@/components/complete';
import { examptionFaceRecognition, getExamptionCompleteInfo } from '../../../../service/traffic/vehicle/examption';

export default {
    name: 'SixYearExamptionIndex', // 六年免检
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        carInput,
        confirmInfo,
        upload,
        checkPhone,
        complete,
        process,
        mail
    },
    computed: {
        ...mapState('common', {
            steps: 'steps',
            currentStep: 'currentStep',
            identifyCard: 'identifyCard',
            binocularCamera: 'binocularCamera'
        }),
        ...mapState('examption', {
            examptionCompleteInfo: 'examptionCompleteInfo',
            phoneNum: 'phoneNum'
        })
    },
    mounted () {
        this.setSteps([
            { name: '业务须知', isActive: true, visible: true, key: 'serverInfo' },
            { name: '身份识别', isActive: false, visible: true, key: 'identifyCardPage' },
            { name: '身份识别', isActive: false, visible: false, key: 'faceYitu' },
            { name: '身份识别', isActive: false, visible: false, key: 'contrast' },
            { name: '身份识别', isActive: false, visible: false, key: 'process' },
            { name: '输入号牌', isActive: false, visible: true, key: 'carInput' },
            { name: '信息确认', isActive: false, visible: true, key: 'confirmInfo' },
            { name: '材料上传', isActive: false, visible: true, key: 'upload' },
            { name: '邮寄信息', isActive: false, visible: true, key: 'mail' },
            { name: '手机校验', isActive: false, visible: true, key: 'checkPhone' },
            { name: '完成提交', isActive: false, visible: false, key: 'process2' },
            { name: '完成提交', isActive: false, visible: true, key: 'complete' }
        ]);
    },
    methods: {
        ...mapMutations('common', ['setSteps', 'changeStep', 'changeTipPage']),
        ...mapMutations('examption', ['setExamptionCarInfo', 'setExamptionCompleteInfo']),
        async handleBusiness () {
            const res = await examptionFaceRecognition({
                name: this.identifyCard.partyName,
                identifiedPhoto: this.binocularCamera
            });
            if (res && res.code === '0') {
                this.changeStep('down');
            } else if (res) {
                this.changeTipPage({ message: res.message });
            }
        },
        async handleBusinessComplete () {
            const info = await getExamptionCompleteInfo({
                verifyMobile: this.phoneNum
            });
            if (info.code === '0') {
                this.setExamptionCompleteInfo(info.result);
                this.changeStep('down');
            } else {
                this.changeTipPage({ message: info.message });
            }
        }
    }
};
</script>
