<template>
    <div>
        <div class="kd-title-content-wrapper kd-page-padding wrap-info">
            <text-table :data="examptionCarInfo"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';
import audioCar from '@/assets/media/trafic/400.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'SixYearExamptionConfirmInfo', // 确认信息
    components: { textTable },
    mounted () {
        this.audioCar = playAudio(audioCar);
        this.audioCar.play();
    },
    beforeDestroy () {
        this.audioCar.destroy();
    },
    computed: {
        ...mapState('examption', {
            examptionCarInfo: 'examptionCarInfo'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        nextStep () { // 下一步
            this.changeStep('down');
        },
        preStep () { // 上一步
            this.changeStep('up');
        }

    }
};
</script>

