<template>
    <div>
        <div class="kd-page-title">输入车牌</div>
        <div class="kd-page-content-wrapper wrap-info">
            <car-input
                ref="carInput"
                last-text="请输入车架编号后六位"
                :last-input="false"
                :get-car-input="getCarInput"
            />
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
                :but-disable="!(carLicense)"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import carInput from '@/components/car-input';
import { CAR_TYPE_OPTIONS } from '@/constant/constant';
import { carInsuranceInfo } from '../../../../../service/traffic/vehicle/examption';

export default {
    name: 'SixYearExamptionCarInput', // 输入车牌
    components: { carInput },
    data () {
        return {
            carType: '02',
            carLicense: '',
            carEngineCode: ''
        };
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    },
    methods: {
        ...mapMutations('examption', ['setExamptionCarInfo']),
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        getCarInput (data) {
            if (data.carType !== undefined) {
                this.carType = data.carType;
            }
            if (data.carLicense !== undefined) {
                this.carLicense = data.carLicense;
            }
            if (data.carEngineCode !== undefined) {
                this.carEngineCode = data.carEngineCode;
            }
        },
        preStep () {
            this.changeStep('up');
        },
        async nextStep () {
            if (this.carLicense) {
                const res = await carInsuranceInfo({
                    plateType: this.carType,
                    plateNumber: this.carLicense,
                    idNum: this.identifyCard.certNumber
                });
                    // 获取 机动车交强险信息
                if (res && res.code === '0') {
                    if (Object.keys(res.result).length === 0) {
                        this.changeTipPage({ message: '未查到您的交强险信息，暂时无法办理此业务' });
                    } else {
                        const carType = CAR_TYPE_OPTIONS.find((item, index) => {
                            return item.value === '02';
                        });
                        this.setExamptionCarInfo({
                            ...res.result,
                            plateType: carType.label
                        });
                        this.changeStep('down');
                    }
                } else if (res) {
                    this.changeTipPage({
                        message: res.message,
                        enableBack: true
                    });
                }
            }
        }
    }
};
</script>

