<template>
    <div class="wrap-info">
        <div class="inputWrap">
            <el-select
                v-model="carType"
                placeholder="请选择"
                class="itemMargin"
                style="width: 150px"
            >
                <el-option
                    v-for="item in carTypeOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                />
            </el-select>
            <span class="car-top itemMargin">{{ carTypeName }}</span>
            <el-select
                v-model="carFirstName"
                placeholder="请选择"
                class="itemName"
                style="width: 70px"
            >
                <el-option
                    v-for="item in CONSTANT.carFirstName"
                    :key="item"
                    :label="item"
                    :value="item"
                />
            </el-select>
            <kd-input
                :ime-type="0"
                ref="carLicenseInput"
                class="car-content itemMargin"
                placeholder="请输入车牌号码"
                :watch-data="getCarLicense"
                :get-focus="getFocus"
                :upper-case="true"
                :limit-number="5"
            />
            <kd-input
                :ime-type="0"
                ref="lastInput"
                v-if="lastInput"
                class="engine-content itemMargin"
                :placeholder="lastText"
                :watch-data="getCarEngineCode"
                :get-focus="getFocus"
                :limit-number="6"
            />
        </div>
        <div class="errorTips">
            {{ errorTip }}
        </div>
        <keyboard
            :get-keyboard-data="getKeyboardData"
            style="width:90%"
        />
    </div>
</template>

<script>
import keyboard from './keyboard';
import { CAR_TYPE_OPTIONS } from '@/constant/constant';
import { mapState } from 'vuex';
import { keyboardHide } from '@/service/common';
import audioCar from '@/assets/media/trafic/100.mp3';
import { playAudio } from '@/utils/media';

export default {
    name: 'CarInput',
    components: { keyboard },
    props: {
        lastText: {
            default: '请输入发动机编号后六位'
        },
        lastInput: {
            default: true
        },
        getCarInput: {
            default: Function
        },
        mp3: {
            default: ''
        },
        carTypeOptions: { type: Array, default: () => CAR_TYPE_OPTIONS }
    },
    computed: {
        ...mapState('common', {
            identifyCard: 'identifyCard'
        })
    },
    data () {
        return {
            carTypeName: this.CONSTANT.carTypeName,
            carFirstName: this.CONSTANT.carFirstName[0],
            carType: '02',
            errorTip: '',
            errorLast: '',
            inputVueEl: null
        };
    },
    watch: {
        carType () {
            if (this.getCarInput) {
                this.getCarInput({ carType: this.carType });
            }
        }
    },
    mounted () {
        this.audioCar = playAudio(this.mp3 || audioCar);
        this.audioCar.play();
        this.$refs.carLicenseInput.startKeyBoard();
    },
    beforeDestroy () {
        this.audioCar.destroy();
        keyboardHide();
    },
    methods: {
        getCarLicense (data) {
            if (this.getCarInput) {
                if (this.isLicenseNo(this.carFirstName + data.toLocaleUpperCase())) {
                    this.getCarInput({ carLicense: this.carFirstName + data.toLocaleUpperCase() });
                    this.errorTip = '';
                } else {
                    this.errorTip = '请填写正确的机动车信息';
                    this.getCarInput({ carLicense: '' });
                }
            }
        },
        getCarEngineCode (data) {
            if (this.getCarInput) {
                if (data.length === 6) {
                    this.getCarInput({ carEngineCode: data.toLocaleUpperCase() });
                    this.errorTip = '';
                } else {
                    this.errorTip = '请填写正确的机动车信息';
                    this.getCarInput({ carEngineCode: '' });
                }
            }
        },
        isLicenseNo (str) {
            if (this.CONSTANT.location === 1) {
                return /^[EU]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/.test(str); // 苏州车牌验证
            } else {
                return /^[A-Z]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/.test(str);
            }
        },
        getKeyboardData (data) {
            if (this.inputVueEl) {
                this.inputVueEl.mouseClick(data);
            }
        },
        getFocus (data) {
            this.inputVueEl = data;
        }
    }
};
</script>

<style lang="scss" scoped>
    .wrap-info {
        display: flex;
        flex-direction: column;
        height: 100%;
        justify-content: center;
        align-items: center;

        ::v-deep .el-select {
            height: 60px;
            box-sizing: border-box;
            border: 1px solid #fff;
            border-radius: 6px;

            .el-input__inner {
                height: 60px;
                background: rgba(255, 255, 255, 0.8);
                border-radius: 6px;
                border: 1px solid #fff;
                font-size: 18px;
                color: #8e9cb9;
                line-height: 37px;
            }

            .el-input .el-select__caret {
                color: #666;
                font-size: 24px;
            }
        }

        ::v-deep .el-icon-arrow-up:before {
            font-size: 8px;
        }
    }

    .car-top {
        height: 60px;
        width: 50px;
        background: rgba(255, 255, 255, 0.8);
        border-radius: 6px;
        border: 1px solid #fff;
        font-size: 18px;
        font-weight: 600;
        color: rgba(60, 75, 120, 1);
        line-height: 60px;
        text-align: center;

    }

    .car-content {
        flex: 1;
        font-size: 18px;
        color: rgba(142, 156, 185, 1);
        line-height: 37px;
        width: 20%;
    }

    .engine-content {
        border-radius: 6px;
        font-size: 18px;
        color: rgba(142, 156, 185, 1);
        line-height: 37px;
        width: 33%;
    }

    .itemMargin {
        margin: 0 5px;
    }

    .errorTips {
        width: 75%;
        color: red;
        text-align: center;
        height: 20px;
        margin: 5px 0;
    }

    .inputWrap {
        display: flex;
        width: 90%;
    }

</style>
